* {
  margin: 0;
  padding: 0;
  font: inherit;
  color: inherit;
}

*,
::after,
::before {
  box-sizing: border-box;
  flex-shrink: 0;
}

:root {
  -webkit-tap-highlight-color: transparent;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  cursor: default;
  overflow-wrap: break-word;
  tab-size: 4;
  line-height: 1.5;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

:where(html, body) {
  height: 100%;
}

:where(img, picture, video, canvas, svg) {
  display: block;
  max-width: 100%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

button {
  background: none;
  border: none;
  padding: 0;
  border: none;
  box-shadow: none;
  overflow: visible;
  white-space: nowrap;
  cursor: pointer;
  appearance: button;
  outline: 1px dotted ButtonText;
}